/* TTS Button Styles */
.tts-button {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 24px;
  height: 24px;
  background: rgba(37, 99, 235, 0.1);
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  opacity: 0.7;
  z-index: 5;
}

/* TTS Icon with embedded SVG */
.tts-icon {
  width: 16px;
  height: 16px;
  display: block;
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 5L8 9H4V15H8L12 19V5Z' stroke='%232563EB' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M15.54 8.46C16.4774 9.39764 17.0039 10.6692 17.0039 12C17.0039 13.3308 16.4774 14.6024 15.54 15.54' stroke='%232563EB' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.tts-button:hover {
  background: rgba(37, 99, 235, 0.2);
  opacity: 1;
  transform: scale(1.1);
}

.tts-button.user {
  background: rgba(255, 255, 255, 0.1);
}

.tts-button.user .tts-icon {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 5L8 9H4V15H8L12 19V5Z' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M15.54 8.46C16.4774 9.39764 17.0039 10.6692 17.0039 12C17.0039 13.3308 16.4774 14.6024 15.54 15.54' stroke='white' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}

.tts-button.user:hover {
  background: rgba(255, 255, 255, 0.2);
}

/* Playing state for TTS button */
.tts-button.playing {
  background: rgba(16, 185, 129, 0.2);
  animation: pulse 1.5s infinite;
}

.tts-button.playing .tts-icon {
  background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 5L8 9H4V15H8L12 19V5Z' stroke='%2310b981' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M15.54 8.46C16.4774 9.39764 17.0039 10.6692 17.0039 12C17.0039 13.3308 16.4774 14.6024 15.54 15.54' stroke='%2310b981' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
}

.tts-button.playing:hover {
  background: rgba(16, 185, 129, 0.3);
}

.tts-button.user.playing {
  background: rgba(255, 255, 255, 0.3);
  animation: pulse 1.5s infinite;
}

.tts-button.user.playing:hover {
  background: rgba(255, 255, 255, 0.4);
}
