/* Action Buttons Styles */
.action-buttons {
  border-top: 1px solid #E5E7EB;
}

.action-buttons-scroll-container {
  width: 100%;
  overflow-x: auto !important; /* Force horizontal scroll to be enabled */
  -webkit-overflow-scrolling: touch; /* iOS için düzgün kaydırma */
  scrollbar-width: thin; /* Firefox için ince scrollbar */
  scrollbar-color: #2563EB #F9FAFB; /* Firefox için scrollbar rengi */
  user-select: none; /* Text seçimini engelle */
  /* Force scroll to be enabled */
  overflow: auto hidden !important;
  /* Ensure scroll is always visible */
  scrollbar-gutter: stable;
}

/* Webkit (Chrome, Safari) için scrollbar stilleri */
.action-buttons-scroll-container::-webkit-scrollbar {
  height: 3px;
}

.action-buttons-scroll-container::-webkit-scrollbar-track {
  background: #F9FAFB;
  border-radius: 3px;
}

.action-buttons-scroll-container::-webkit-scrollbar-thumb {
  background: #2563EB;
  border-radius: 3px;
}

.action-buttons-wrapper {
  display: flex;
  gap: 12px;
  padding: 5px;
  min-width: max-content; /* İçerik kadar genişlik */
  /* Force horizontal scroll to be triggered */
  min-width: calc(100% + 50px); /* Force content to be wider than container */
}

.secondary-button {
  flex: 0 0 auto; /* Sabit genişlik yerine içerik kadar */
  padding: 6px 10px;
  border: none;
  background-color: #F9FAFB;
  color: #1F2937;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s;
  white-space: nowrap; /* Metni tek satırda tut */
  min-width: fit-content; /* İçerik kadar minimum genişlik */
  font-size: 0.8rem;
}

.secondary-button:hover {
  background-color: #E5E7EB;
}
