/* General Message Styles */
.message {
  padding: 8px 8px;
  border-radius: 12px;
  font-size: 0.85rem;
  position: relative;
  width: auto; /* Genişliği içeriğe göre ayarla */
  overflow: hidden; /* İçerik taşmasını engelle */
  word-wrap: break-word;
  box-sizing: border-box;
}

.agent-message {
  background-color: #F9FAFB;
  color: #1F2937;
  max-width: 90%; /* Agent mesajları için maksimum genişlik */
  width: auto; /* İçeriğe göre genişlik */
  min-width: fit-content; /* Minimum genişlik içeriğe göre */
}

/* Message Content Wrapper */
.message-content-wrapper {
  width: 90%; /* Tam genişlik */
}

/* Message Content */
.message-content {
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  word-wrap: break-word;
}

.message h4 {
  margin: 0 0 8px 0;
  font-size: 0.9rem;
}

.message p {
  margin: 0;
  text-align: left;
}

/* Feedback Button Styles */
.feedback-container {
  display: flex;
  gap: 8px;
  margin-top: 8px;
  justify-content: flex-start;
}

.feedback-button {
  background: none;
  border: 1px solid #D1D5DB;
  border-radius: 16px;
  padding: 4px 12px;
  font-size: 12px;
  cursor: pointer;
  color: #6B7280;
  transition: all 0.2s;
}

.feedback-button:hover {
  background-color: #10B981;
  color: white;
  border-color: #10B981;
}

.feedback-button.negative:hover {
  background-color: #EF4444;
  border-color: #EF4444;
}
