/* No Data Message Styles */
.message {
  padding: 8px 8px;
  border-radius: 12px;
  font-size: 0.85rem;
  position: relative;
  width: auto; /* Genişliği içeriğe göre ayarla */
  overflow: hidden; /* İçerik taşmasını engelle */
  word-wrap: break-word;
  box-sizing: border-box;
}

.bot-message {
  background-color: #F9FAFB;
  color: #1F2937;
  max-width: 90%; /* Bot mesajları için maksimum genişlik */
  width: auto; /* İçeriğe göre genişlik */
  min-width: fit-content; /* Minimum genişlik içeriğe göre */
  overflow: hidden;
  word-wrap: break-word;
  box-sizing: border-box;
}

/* Message Content */
.message-content {
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  word-wrap: break-word;
}

/* No Data Message Container */
.no-data-message {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  padding: 20px;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  border-radius: 16px;
  border: 1px solid #e2e8f0;
  margin-bottom: 16px;
  max-width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  word-wrap: break-word;
  scrollbar-width: thin;
  scrollbar-color: #cbd5e1 #f1f5f9;
}

/* NoDataMessage için özel scrollbar stilleri */
.no-data-message::-webkit-scrollbar {
  height: 6px;
}

.no-data-message::-webkit-scrollbar-track {
  background: #f1f5f9;
  border-radius: 3px;
}

.no-data-message::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 3px;
}

.no-data-message::-webkit-scrollbar-thumb:hover {
  background: #94a3b8;
}

.no-data-icon {
  flex-shrink: 0;
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.no-data-content {
  flex: 1;
  min-width: 0;
  max-width: 100%;
  overflow: visible;
  white-space: nowrap;
}

.no-data-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0 0 8px 0;
  line-height: 1.4;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.no-data-description {
  font-size: 0.95rem;
  color: #6b7280;
  margin: 0 0 16px 0;
  line-height: 1.5;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.search-details {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.9rem;
  color: #6b7280;
  background: rgba(59, 130, 246, 0.1);
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid rgba(59, 130, 246, 0.2);
  max-width: 100%;
  overflow: visible;
  flex-wrap: nowrap;
  white-space: nowrap;
}

.search-label {
  font-weight: 500;
  color: #374151;
}

.search-query {
  font-weight: 600;
  color: #1f2937;
  background: rgba(59, 130, 246, 0.15);
  padding: 2px 6px;
  border-radius: 4px;
  max-width: none;
  overflow: visible;
  text-overflow: unset;
  white-space: nowrap;
  flex-shrink: 0;
}

.search-results {
  color: #6b7280;
  font-style: italic;
}

/* Suggestions Section */
.suggestions-section {
  margin-top: 16px;
  max-width: 100%;
  overflow: visible;
}

.suggestions-title {
  font-size: 0.9rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 12px 0;
  white-space: nowrap;
}

.suggestions-list {
  display: flex;
  flex-direction: row;
  gap: 8px;
  max-width: 100%;
  overflow: visible;
  flex-wrap: nowrap;
}

.suggestion-item {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background: rgba(59, 130, 246, 0.05);
  border: 1px solid rgba(59, 130, 246, 0.1);
  border-radius: 8px;
  font-size: 0.85rem;
  color: #374151;
  max-width: none;
  overflow: visible;
  word-wrap: normal;
  white-space: nowrap;
  flex-shrink: 0;
}

.suggestion-bullet {
  color: #3b82f6;
  font-weight: bold;
  flex-shrink: 0;
}

.suggestion-text {
  flex: 1;
  min-width: 0;
  overflow: visible;
  text-overflow: unset;
  white-space: nowrap;
}

.feedback-section {
  border-top: 1px solid #e5e7eb;
  padding-top: 16px;
  margin-top: 16px;
  max-width: 100%;
  overflow: visible;
  white-space: nowrap;
}

.feedback-question {
  font-size: 0.9rem;
  color: #6b7280;
  margin: 0 0 12px 0;
  text-align: center;
}

.feedback-buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-bottom: 12px;
  flex-wrap: nowrap;
  overflow: visible;
}

.feedback-btn {
  padding: 8px 16px;
  border: 1px solid;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 6px;
}

.feedback-btn.helpful {
  background: #dcfce7;
  border-color: #22c55e;
  color: #166534;
}

.feedback-btn.not-helpful {
  background: #fee2e2;
  border-color: #ef4444;
  color: #991b1b;
}

.feedback-btn:hover:not(.disabled) {
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.feedback-btn.disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.feedback-thanks {
  text-align: center;
  font-size: 0.9rem;
  color: #059669;
  font-weight: 500;
  margin: 0;
  padding: 8px;
  background: rgba(34, 197, 94, 0.1);
  border-radius: 6px;
  border: 1px solid rgba(34, 197, 94, 0.2);
}
