/* Notification Widget Styles - ConvState AI Widget */
.notification-widget {
  position: fixed !important;
  bottom: 20px !important; /* Same level as bottom-avatar */
  right: 100px !important; /* Position to the left of bottom-avatar */
  z-index: 9999 !important; /* Higher z-index to ensure visibility */
  cursor: pointer;
  user-select: none;
  transition: all 0.3s ease;
  opacity: 0;
  transform: translateY(20px);
  pointer-events: auto;
}

/* Animation Classes */
.notification-widget-fade-in {
  animation: fadeInUp 0.5s ease forwards !important;
}

.notification-widget-slide-in {
  animation: slideInRight 0.5s ease forwards !important;
}

.notification-widget-bounce {
  animation: bounceIn 0.6s ease forwards !important;
}

.notification-widget-closing {
  animation: fadeOutDown 0.3s ease forwards !important;
}

/* Speech Bubble */
.notification-speech-bubble {
  background: white !important;
  border-radius: 20px !important;
  padding: 16px 20px !important;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15) !important;
  position: relative !important;
  min-width: 200px !important;
  max-width: 280px !important;
  transition: transform 0.2s ease !important;
}

.notification-speech-bubble::after {
  content: '' !important;
  position: absolute !important;
  right: -8px !important;
  top: 50% !important;
  transform: translateY(-50%) !important;
  width: 0 !important;
  height: 0 !important;
  border-left: 8px solid white !important;
  border-top: 8px solid transparent !important;
  border-bottom: 8px solid transparent !important;
}

.notification-widget:hover .notification-speech-bubble {
  transform: translateY(-2px) !important;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.2) !important;
}

/* Notification Content */
.notification-content {
  display: flex !important;
  flex-direction: column !important;
  gap: 8px !important;
}

.notification-header {
  display: flex !important;
  align-items: center !important;
  gap: 8px !important;
  transform: scale(0.9) !important; /* 10% smaller */
  transform-origin: left center !important;
}

.notification-sender {
  font-size: 12px !important;
  font-weight: 600 !important;
  color: #8B5CF6 !important;
  text-transform: uppercase !important;
  letter-spacing: 0.5px !important;
}

.notification-message {
  font-size: 14px !important;
  color: #374151 !important;
  line-height: 1.4 !important;
  font-weight: 500 !important;
}

/* Close Button */
.notification-close-btn {
  position: absolute !important;
  top: 8px !important;
  right: 8px !important;
  background: none !important;
  border: none !important;
  font-size: 18px !important;
  color: #9CA3AF !important;
  cursor: pointer !important;
  width: 24px !important;
  height: 24px !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  border-radius: 50% !important;
  transition: all 0.2s ease !important;
}

.notification-close-btn:hover {
  background: #F3F4F6 !important;
  color: #6B7280 !important;
}


/* Snowflake Icon */
.notification-icon-symbol {
  position: relative;
  width: 30px;
  height: 30px;
}

.icon-snowflake {
  position: relative;
  width: 100%;
  height: 100%;
}

.snowflake-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 6px;
  height: 6px;
  background: white;
  border-radius: 50%;
}

.snowflake-arm {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 2px;
  height: 8px;
  background: white;
  border-radius: 1px;
  transform-origin: 50% 0%;
}

.snowflake-arm-1 { transform: translate(-50%, -50%) rotate(0deg); }
.snowflake-arm-2 { transform: translate(-50%, -50%) rotate(60deg); }
.snowflake-arm-3 { transform: translate(-50%, -50%) rotate(120deg); }
.snowflake-arm-4 { transform: translate(-50%, -50%) rotate(180deg); }
.snowflake-arm-5 { transform: translate(-50%, -50%) rotate(240deg); }
.snowflake-arm-6 { transform: translate(-50%, -50%) rotate(300deg); }

.snowflake-diagonal {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 2px;
  background: white;
  border-radius: 1px;
  transform: translate(-50%, -50%) rotate(45deg);
}

/* Decorative Dots */
.notification-dots {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.dot {
  position: absolute;
  width: 3px;
  height: 3px;
  background: white;
  border-radius: 50%;
  opacity: 0.7;
}

.dot-1 { top: 15%; left: 20%; animation: float 3s ease-in-out infinite; }
.dot-2 { top: 25%; right: 15%; animation: float 3s ease-in-out infinite 0.5s; }
.dot-3 { bottom: 30%; left: 10%; animation: float 3s ease-in-out infinite 1s; }
.dot-4 { bottom: 20%; right: 25%; animation: float 3s ease-in-out infinite 1.5s; }
.dot-5 { top: 50%; left: 5%; animation: float 3s ease-in-out infinite 2s; }

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fadeOutDown {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(20px);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-3px);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .notification-widget {
    right: 100px !important; /* Position to the left of bottom-avatar on mobile */
    bottom: 20px !important; /* Same level as bottom-avatar on mobile */
  }
  
  .notification-speech-bubble {
    min-width: 180px;
    max-width: 250px;
  }
  
  
  .notification-icon-symbol {
    width: 25px;
    height: 25px;
  }
}

/* Color Theme Variations */
.notification-widget[data-theme="blue"] .notification-sender {
  color: #3B82F6;
}

.notification-widget[data-theme="green"] .notification-sender {
  color: #10B981;
}

.notification-widget[data-theme="orange"] .notification-sender {
  color: #F59E0B;
}
