/* Message Styles */
.message-group {
  display: flex;
  gap: 10px;
  margin-bottom: 12px; 
  margin-top: 12px;
  text-align: left;
  width: 100%; /* Container genişliğini kullan */
}

.message-group.user {
  justify-content: flex-end;
}

.message {
  padding: 8px 8px;
  border-radius: 12px;
  font-size: 0.85rem;
  position: relative;
  width: 120px;
  width: auto; /* Genişliği içeriğe göre ayarla */
  overflow: hidden; /* İçerik taşmasını engelle */
  word-wrap: break-word;
  box-sizing: border-box;
}

.agent-message {
  background-color: #F9FAFB;
  color: #1F2937;
  max-width: 90%; /* Agent mesajları için maksimum genişlik */
  width: auto; /* İçeriğe göre genişlik */
  min-width: fit-content; /* Minimum genişlik içeriğe göre */
}

.bot-message {
  background-color: #F9FAFB;
  color: #1F2937;
  max-width: 90%; /* Bot mesajları için maksimum genişlik */
  width: auto; /* İçeriğe göre genişlik */
  min-width: fit-content; /* Minimum genişlik içeriğe göre */
  overflow: hidden;
  word-wrap: break-word;
  box-sizing: border-box;
}

.user-message {
  background-color: #2563EB;
  color: white;
  max-width: 80%; /* User mesajları için maksimum genişlik */
  width: 120px; /* İçeriğe göre genişlik */
  min-width: fit-content; /* Minimum genişlik içeriğe göre */
}

.message h4 {
  margin: 0 0 8px 0;
  font-size: 0.9rem;
}

.message p {
  margin: 0;
  text-align: left;
}

/* Message Content Wrapper */
.message-content-wrapper {
  width: 90%; /* Tam genişlik */
}

/* Message Content */
.message-content {
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  word-wrap: break-word;
}

/* Agent Avatar */
.agent-avatar {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  overflow: hidden;
  flex-shrink: 0;
}

.agent-avatar.large {
  width: 37px;
  height: 37px;
}

.agent-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
