/* Order Message Styles */
.message {
  padding: 8px 8px;
  border-radius: 12px;
  font-size: 0.85rem;
  position: relative;
  width: auto; /* Genişliği içeriğe göre ayarla */
  overflow: hidden; /* İçerik taşmasını engelle */
  word-wrap: break-word;
  box-sizing: border-box;
}

.agent-message {
  background-color: #F9FAFB;
  color: #1F2937;
  max-width: 90%; /* Agent mesajları için maksimum genişlik */
  width: auto; /* İçeriğe göre genişlik */
  min-width: fit-content; /* Minimum genişlik içeriğe göre */
}

/* Message Content Wrapper */
.message-content-wrapper {
  width: 90%; /* Tam genişlik */
}

/* Message Content */
.message-content {
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  word-wrap: break-word;
}

/* Order Input Container */
.order-input-container {
  display: flex;
  gap: 8px;
  margin: 12px 0;
  align-items: center;
}

.order-number-input {
  flex: 1;
  padding: 8px 12px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.2s;
}

.order-number-input:focus {
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

.track-order-button {
  padding: 8px 16px;
  background-color: #2563eb;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
  white-space: nowrap;
}

.track-order-button:hover:not(:disabled) {
  background-color: #1d4ed8;
}

.track-order-button:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

/* Order Tracking Details */
.order-tracking-details {
  background: #f8fafc;
  border-radius: 12px;
  padding: 16px;
  margin: 12px 0;
}

.order-tracking-details h3 {
  margin: 0 0 16px 0;
  color: #1f2937;
  font-size: 18px;
  font-weight: 600;
}

.order-header {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 16px;
  padding: 12px;
  background: white;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.order-id {
  font-weight: 600;
  color: #2563eb;
  font-size: 16px;
}

.order-status {
  color: #059669;
  font-weight: 500;
}

.order-date {
  color: #6b7280;
  font-size: 14px;
}

.order-items {
  margin-bottom: 16px;
}

.order-items h4 {
  margin: 0 0 12px 0;
  color: #1f2937;
  font-size: 16px;
  font-weight: 600;
}

.order-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background: white;
  border-radius: 6px;
  margin-bottom: 8px;
  border: 1px solid #f3f4f6;
}

.item-name {
  flex: 1;
  font-weight: 500;
  color: #1f2937;
}

.item-quantity {
  color: #6b7280;
  margin: 0 8px;
}

.item-price {
  font-weight: 600;
  color: #059669;
}

.shipping-info {
  margin-bottom: 16px;
}

.shipping-info h4 {
  margin: 0 0 12px 0;
  color: #1f2937;
  font-size: 16px;
  font-weight: 600;
}

.shipping-detail {
  display: flex;
  justify-content: space-between;
  padding: 8px 12px;
  background: white;
  border-radius: 6px;
  margin-bottom: 6px;
  border: 1px solid #f3f4f6;
}

.shipping-detail strong {
  color: #374151;
}

.order-message {
  background: white;
  padding: 12px;
  border-radius: 8px;
  border-left: 4px solid #2563eb;
  margin-top: 16px;
}

.order-message p {
  margin: 0;
  color: #1f2937;
  line-height: 1.5;
}

/* Simplified Cargo Tracking Styles */
.cargo-tracking-simple {
  background: #f8fafc;
  border-radius: 12px;
  padding: 16px;
  margin: 12px 0;
}

.cargo-tracking-simple h3 {
  margin: 0 0 16px 0;
  color: #1f2937;
  font-size: 18px;
  font-weight: 600;
}

.cargo-info-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
}

.cargo-info-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  background: white;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.cargo-info-item strong {
  color: #374151;
  font-weight: 600;
  flex-shrink: 0;
  margin-right: 12px;
}

.cargo-info-item span {
  color: #1f2937;
  text-align: right;
}

.status-badge {
  padding: 4px 8px;
  border-radius: 6px;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-badge.in_transit {
  background-color: #fef3c7;
  color: #92400e;
}

.status-badge.delivered {
  background-color: #d1fae5;
  color: #065f46;
}

.status-badge.pending {
  background-color: #e0e7ff;
  color: #3730a3;
}

.tracking-number {
  font-family: 'Courier New', monospace;
  font-weight: 600;
  color: #2563eb;
  letter-spacing: 1px;
}

/* Feature Disabled Message */
.feature-disabled-message {
  text-align: center;
  padding: 20px;
}

.feature-disabled-message h3 {
  color: #92400E;
  margin-bottom: 16px;
}

.feature-disabled-message p {
  color: #92400E;
  font-size: 14px;
  margin: 0;
}

/* Cargo Not Found Message */
.cargo-not-found-message {
  text-align: center;
  padding: 20px;
}

.cargo-not-found-message h3 {
  color: #DC2626;
  margin-bottom: 16px;
}

.cargo-not-found-message p {
  color: #DC2626;
  font-size: 14px;
  margin: 0;
}

/* Cargo Error Message */
.cargo-error-message {
  text-align: center;
  padding: 20px;
}

.cargo-error-message h3 {
  color: #DC2626;
  margin-bottom: 16px;
}

.cargo-error-message p {
  color: #DC2626;
  font-size: 14px;
  margin: 0;
}

/* Feedback Button Styles */
.feedback-container {
  display: flex;
  gap: 8px;
  margin-top: 8px;
  justify-content: flex-start;
}

.feedback-button {
  background: none;
  border: 1px solid #D1D5DB;
  border-radius: 16px;
  padding: 4px 12px;
  font-size: 12px;
  cursor: pointer;
  color: #6B7280;
  transition: all 0.2s;
}

.feedback-button:hover {
  background-color: #10B981;
  color: white;
  border-color: #10B981;
}

.feedback-button.negative:hover {
  background-color: #EF4444;
  border-color: #EF4444;
}
