/* Welcome Message Styles */
.welcome-message-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 5px 5px;
  background: #f8fafc;
  border-radius: 12px;
  margin: 2px 2px;
  position: relative;
  min-height: 120px;
  border: 1px solid #e2e8f0;
}

.welcome-logo {
  width: 51px;
  height: 51px;
  margin-bottom: 8px;
  margin-top: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  border-radius: 50%;
  box-shadow: 1 2px 6px rgba(0, 0, 0, 0.1);
  border: 1px solid var(--widget-primary-color, #2563EB);
}

.welcome-logo img {
  width: 50px;
  height: 50px;
  object-fit: contain;
}

.welcome-agent-name {
  font-size: 1.1rem;
  font-weight: 700;
  color: #1f2937;
  margin-bottom: 6px;
  text-align: center;
}

.welcome-text {
  font-size: 0.8rem;
  color: #4b5563;
  line-height: 1.3;
  margin-bottom: 8px;
  max-width: 240px;
  margin-bottom: 12px;
  text-align: center;
}

.welcome-tts {
  position: absolute;
  top: 8px;
  right: 8px;
}
