/* Chat Footer Styles */
.chat-footer {
  padding: 6px;
  text-align: center;
  font-size: 0.75rem;
  color: #6B7280;
  border-top: 1px solid #E5E7EB;
}

.separator {
  margin: 0 8px;
}

.privacy-link {
  color: #6B7280;
  text-decoration: none;
}

.privacy-link:hover {
  text-decoration: underline;
}
