/* Input Area Styles */
.input-area {
  border-top: 1px solid #e5e7eb;
  display: flex;
  gap: 12px;
}

.input-wrapper {
  position: relative;
  flex: 1;
  display: flex;
  align-items: center;
}

#messageInput {
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  flex: 1;
  font-size: 0.85rem;
  outline: none;
  padding: 8px 50px 8px 12px;
  transition: border-color 0.2s;
}

#messageInput:focus {
  border-color: #2563eb;
}

.send-button {
  align-items: center;
  background: none;
  border: none;
  border-radius: 6px;
  color: rgba(107, 114, 128, 0.6);
  cursor: pointer;
  display: flex;
  height: 24px;
  justify-content: center;
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  transition: color 0.2s;
  width: 24px;
}

.send-button:hover {
  color: rgba(107, 114, 128, 0.8);
}

.send-button:disabled {
  color: rgba(107, 114, 128, 0.3);
  cursor: not-allowed;
}

.voice-button {
  background: none;
  border: none;
  border-radius: 8px;
  color: #1f2937;
  cursor: pointer;
  margin: 0 4px;
  padding: 8px;
  position: relative;
  transition: all 0.2s ease;
}

.voice-button:hover {
  background-color: #f3f4f6;
  transform: scale(1.05);
}

.voice-button:active {
  transform: scale(0.95);
}

.voice-button:hover {
  background-color: #f9fafb;
}

.voice-button.recording {
  background-color: transparent;
  color: #2563eb;
}

/* Voice wave animations */
.voice-wave {
  transform-origin: center bottom;
}

/* CSS fallback animations for browsers that don't support SVG animations */
@keyframes waveAnimation1 {
  0%, 100% { height: 2px; transform: translateY(0); }
  50% { height: 8px; transform: translateY(-3px); }
}

@keyframes waveAnimation2 {
  0%, 100% { height: 6px; transform: translateY(0); }
  50% { height: 12px; transform: translateY(-3px); }
}

@keyframes waveAnimation3 {
  0%, 100% { height: 8px; transform: translateY(0); }
  50% { height: 16px; transform: translateY(-4px); }
}

@keyframes waveAnimation4 {
  0%, 100% { height: 6px; transform: translateY(0); }
  50% { height: 12px; transform: translateY(-3px); }
}

@keyframes waveAnimation5 {
  0%, 100% { height: 2px; transform: translateY(0); }
  50% { height: 8px; transform: translateY(-3px); }
}

.voice-button.recording .wave-1 {
  animation: waveAnimation1 0.6s infinite;
}

.voice-button.recording .wave-2 {
  animation: waveAnimation2 0.8s infinite;
}

.voice-button.recording .wave-3 {
  animation: waveAnimation3 0.7s infinite;
}

.voice-button.recording .wave-4 {
  animation: waveAnimation4 0.9s infinite;
}

.voice-button.recording .wave-5 {
  animation: waveAnimation5 0.65s infinite;
}
