.api-error-message {
  background: linear-gradient(135deg, #fef2f2 0%, #fef7f7 100%);
  border: 1px solid #fecaca;
  border-left: 4px solid #ef4444;
  border-radius: 12px;
  margin: 12px 0;
  padding: 16px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  animation: slideInFromTop 0.3s ease-out;
  position: relative;
  overflow: hidden;
}

.api-error-message::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #ef4444, #f87171, #fca5a5);
  animation: shimmer 2s infinite;
}

@keyframes slideInFromTop {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.api-error-header {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 12px;
}

.api-error-icon {
  font-size: 24px;
  flex-shrink: 0;
  margin-top: 2px;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

.api-error-content {
  flex: 1;
  min-width: 0;
}

.api-error-title {
  font-size: 16px;
  font-weight: 600;
  color: #dc2626;
  margin: 0 0 4px 0;
  line-height: 1.4;
}

.api-error-description {
  font-size: 14px;
  color: #7f1d1d;
  margin: 0 0 8px 0;
  line-height: 1.5;
}

.api-error-status {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 8px;
}

.status-label {
  font-size: 12px;
  color: #991b1b;
  font-weight: 500;
}

.status-code {
  background: #fecaca;
  color: #991b1b;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 600;
  font-family: 'Courier New', monospace;
}

.api-error-actions {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-shrink: 0;
}

.api-error-details-btn,
.api-error-close-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 4px;
  border-radius: 4px;
  font-size: 16px;
  transition: background-color 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
}

.api-error-details-btn:hover {
  background: rgba(239, 68, 68, 0.1);
}

.api-error-close-btn:hover {
  background: rgba(239, 68, 68, 0.1);
}

.api-error-details {
  margin-top: 12px;
  padding: 12px;
  background: rgba(239, 68, 68, 0.05);
  border-radius: 8px;
  border: 1px solid rgba(239, 68, 68, 0.2);
}

.api-error-details h4 {
  font-size: 14px;
  font-weight: 600;
  color: #991b1b;
  margin: 0 0 8px 0;
}

.api-error-details-content {
  font-size: 12px;
  color: #7f1d1d;
  background: #fef2f2;
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #fecaca;
  margin: 0;
  overflow-x: auto;
  white-space: pre-wrap;
  word-break: break-word;
  font-family: 'Courier New', monospace;
  line-height: 1.4;
}

.api-error-footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  margin-top: 12px;
  padding-top: 12px;
  border-top: 1px solid rgba(239, 68, 68, 0.2);
}

.api-error-retry-btn,
.api-error-redirect-btn {
  background: linear-gradient(135deg, #ef4444 0%, #dc2626 100%);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  gap: 6px;
  box-shadow: 0 2px 4px rgba(239, 68, 68, 0.3);
}

.api-error-retry-btn:hover,
.api-error-redirect-btn:hover {
  background: linear-gradient(135deg, #dc2626 0%, #b91c1c 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(239, 68, 68, 0.4);
}

.api-error-retry-btn:active,
.api-error-redirect-btn:active {
  transform: translateY(0);
  box-shadow: 0 2px 4px rgba(239, 68, 68, 0.3);
}

.api-error-redirect-btn {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  box-shadow: 0 2px 4px rgba(59, 130, 246, 0.3);
}

.api-error-redirect-btn:hover {
  background: linear-gradient(135deg, #2563eb 0%, #1d4ed8 100%);
  box-shadow: 0 4px 8px rgba(59, 130, 246, 0.4);
}

.api-error-auto-hide {
  font-size: 12px;
  color: #991b1b;
  opacity: 0.7;
  display: flex;
  align-items: center;
  gap: 4px;
}

.api-error-auto-hide::before {
  content: '⏱️';
  font-size: 14px;
}

/* Responsive design */
@media (max-width: 480px) {
  .api-error-message {
    margin: 8px 0;
    padding: 12px;
  }
  
  .api-error-header {
    gap: 8px;
  }
  
  .api-error-icon {
    font-size: 20px;
  }
  
  .api-error-title {
    font-size: 14px;
  }
  
  .api-error-description {
    font-size: 13px;
  }
  
  .api-error-footer {
    flex-direction: column;
    align-items: stretch;
    gap: 8px;
  }
  
  .api-error-retry-btn,
  .api-error-redirect-btn {
    justify-content: center;
    width: 100%;
  }
}

/* Dark mode support */
@media (prefers-color-scheme: dark) {
  .api-error-message {
    background: linear-gradient(135deg, #1f1f1f 0%, #2a2a2a 100%);
    border-color: #4c1d1d;
    color: #f5f5f5;
  }
  
  .api-error-title {
    color: #fca5a5;
  }
  
  .api-error-description {
    color: #fecaca;
  }
  
  .api-error-details {
    background: rgba(239, 68, 68, 0.1);
    border-color: rgba(239, 68, 68, 0.3);
  }
  
  .api-error-details h4 {
    color: #fca5a5;
  }
  
  .api-error-details-content {
    background: #1f1f1f;
    color: #fecaca;
    border-color: #4c1d1d;
  }
  
  .api-error-auto-hide {
    color: #fca5a5;
  }
}
