/* Cargo Tracking Message Styles */
.message {
  padding: 8px 8px;
  border-radius: 12px;
  font-size: 0.85rem;
  position: relative;
  width: auto; /* Genişliği içeriğe göre ayarla */
  overflow: hidden; /* İçerik taşmasını engelle */
  word-wrap: break-word;
  box-sizing: border-box;
}

.agent-message {
  background-color: #F9FAFB;
  color: #1F2937;
  max-width: 90%; /* Agent mesajları için maksimum genişlik */
  width: auto; /* İçeriğe göre genişlik */
  min-width: fit-content; /* Minimum genişlik içeriğe göre */
}

/* Message Content Wrapper */
.message-content-wrapper {
  width: 90%; /* Tam genişlik */
}

/* Message Content */
.message-content {
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  word-wrap: break-word;
}

/* Cargo Tracking Input */
.cargo-tracking-input {
  margin-bottom: 16px;
}

.cargo-tracking-input input {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #D1D5DB;
  border-radius: 6px;
  font-size: 14px;
  outline: none;
  margin-bottom: 8px;
}

.cargo-tracking-input input:focus {
  border-color: #3B82F6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.cargo-tracking-input button {
  width: 100%;
  padding: 8px 10px;
  background-color: #3B82F6;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  font-size: 14px;
  font-weight: 500;
  opacity: 1;
  transition: all 0.2s ease;
}

.cargo-tracking-input button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Cargo Tracking Result */
.cargo-tracking-result {
  background: #F9FAFB;
  padding: 16px;
  border-radius: 8px;
  border: 1px solid #E5E7EB;
  margin-bottom: 16px;
}

.cargo-tracking-result.feature-disabled {
  background: #FEF3C7;
  border-color: #F59E0B;
}

.cargo-tracking-result h4 {
  font-size: 16px;
  font-weight: 600;
  margin: 0 0 12px 0;
  color: #111827;
}

.cargo-tracking-result .cargo-info-grid {
  display: grid;
  gap: 8px;
}

.cargo-tracking-result .cargo-info-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
  border-bottom: 1px solid #E5E7EB;
}

.cargo-tracking-result .cargo-info-item:last-child {
  border-bottom: none;
}

.cargo-tracking-result .cargo-info-item span:first-child {
  font-size: 14px;
  color: #6B7280;
  font-weight: 500;
}

.cargo-tracking-result .cargo-info-item span:last-child {
  font-size: 14px;
  font-weight: 500;
  color: #1F2937;
}

.cargo-tracking-result .tracking-url {
  margin-top: 12px;
  text-align: center;
}

.cargo-tracking-result .tracking-url a {
  color: #3B82F6;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  transition: color 0.2s ease;
}

.cargo-tracking-result .tracking-url a:hover {
  color: #1D4ED8;
  text-decoration: underline;
}

/* Feature Disabled Styles */
.feature-disabled-message {
  text-align: center;
  padding: 20px;
}

.feature-disabled-message h3 {
  color: #92400E;
  margin-bottom: 16px;
  font-size: 18px;
  font-weight: 600;
}

.feature-disabled-message p {
  color: #92400E;
  font-size: 14px;
  margin: 0;
}

/* Error Message Styles */
.error-message {
  text-align: center;
  padding: 20px;
}

.error-message p {
  color: #DC2626;
  margin: 0;
  font-size: 14px;
}

/* Feedback Button Styles */
.feedback-container {
  display: flex;
  gap: 8px;
  margin-top: 8px;
  justify-content: flex-start;
}

.feedback-button {
  background: none;
  border: 1px solid #D1D5DB;
  border-radius: 16px;
  padding: 4px 12px;
  font-size: 12px;
  cursor: pointer;
  color: #6B7280;
  transition: all 0.2s;
}

.feedback-button:hover {
  background-color: #10B981;
  color: white;
  border-color: #10B981;
}

.feedback-button.negative:hover {
  background-color: #EF4444;
  border-color: #EF4444;
}

.feedback-thanks {
  text-align: center;
  font-size: 0.9rem;
  color: #059669;
  font-weight: 500;
  margin: 0;
  padding: 8px;
  background: rgba(34, 197, 94, 0.1);
  border-radius: 6px;
  border: 1px solid rgba(34, 197, 94, 0.2);
}
